'use strict';

var varint = require('varint');

var uint8ArrayFromString = require('uint8arrays/from-string');

var uint8ArrayToString = require('uint8arrays/to-string');

var encoder = require('./encoder');

function stringEncodingLength(val) {
  var len = uint8ArrayFromString(val).byteLength;
  return varint.encodingLength(len) + len;
}

function stringEncode(val, buffer, dataView, offset) {
  var oldOffset = offset;
  var len = uint8ArrayFromString(val).byteLength;
  varint.encode(len, buffer, offset, 'utf-8');
  offset += varint.encode.bytes;
  var arr = uint8ArrayFromString(val);
  buffer.set(arr, offset);
  offset += arr.length;
  stringEncode.bytes = offset - oldOffset;
}

function stringDecode(buffer, dataView, offset) {
  var oldOffset = offset;
  var len = varint.decode(buffer, offset);
  offset += varint.decode.bytes;
  var val = uint8ArrayToString(buffer.subarray(offset, offset + len));
  offset += len;
  stringDecode.bytes = offset - oldOffset;
  return val;
}

module.exports = encoder(2, stringEncode, stringDecode, stringEncodingLength);