'use strict';

var CID = require('cids');

var uint8ArrayEquals = require('uint8arrays/equals');

var rmLink = function rmLink(dagNode, nameOrCid) {
  var predicate = null; // It's a name

  if (typeof nameOrCid === 'string') {
    predicate = function predicate(link) {
      return link.Name === nameOrCid;
    };
  } else if (nameOrCid instanceof Uint8Array || CID.isCID(nameOrCid)) {
    predicate = function predicate(link) {
      return uint8ArrayEquals(link.Hash, nameOrCid);
    };
  }

  if (predicate) {
    var links = dagNode.Links;
    var index = 0;

    while (index < links.length) {
      var link = links[index];

      if (predicate(link)) {
        links.splice(index, 1);
      } else {
        index++;
      }
    }
  } else {
    throw new Error('second arg needs to be a name or CID');
  }
};

module.exports = rmLink;